<template>
    <div class="slider-widget">
        <ul>
            <li>
                <banner-widget :cur-class="classes[0]"></banner-widget>
            </li>
        </ul>
    </div>
</template>

<script>
    import Banner from '@/components/widget/banner-widget';                //banner

    export default {
        name: "slider-widget",
        props: ["classes"],
        components: {
            'banner-widget': Banner
        }
    }
</script>

<style scoped>
    @import "../../assets/css/slider-widget.css";
</style>


