<template>
    <div class="container">
        <h2 class="main-title-widget">
            <a class="mt-w-link" @click="back">返回上级</a>
        </h2>
        <div class="grid">
            <div class="col-7">
                <div class="pannel-widget pad-w-1">

                    <!--富文本编辑器-->
                    <tiny-editor v-model="tinymce.html" :init="tinymce.setting"></tiny-editor>

                </div>
            </div>
            <div class="col-5 pad-left-2">
                <div class="pannel-widget">
                    <h3 class="base-title-widget bt-w-blue">文章信息</h3>
                    <div class="list-text-widget pad-w-2">
                        <div class="lt-w-item">
                            <span>标题</span><input type="text" class="lt-w-item-after" placeholder="请输入标题"
                                                  v-model="title"/>
                        </div>
                    </div>
                    <div class="pcenter">
                        <div class="pad-h-2"></div>
                        <span class="base-btn-widget" @click="publish_article">发布文章</span>
                        <div class="pad-h-1"></div>
                    </div>
                </div>
            </div>
        </div>

        <div class="pad-h-5"></div>
    </div>
</template>

<script>
    import tinymce from 'tinymce/tinymce'
    import 'tinymce/themes/modern/theme'
    import Editor from '@tinymce/tinymce-vue'
    import 'tinymce/plugins/link'
    import 'tinymce/plugins/image'
    import 'tinymce/plugins/emoticons'

    export default {
        name: "editor",
        data() {
            return {

                /*文章标题*/
                title: "",

                /*编辑模式（默认为新增模式）*/
                edit_mode: "add",

                /*富文本编辑器*/
                tinymce: {
                    html: "",
                    setting: {
                        menu: {},
                        content_style: "img {max-width: 100%; height: auto}",
                        file_picker_callback: function (cb, value, meta) {

                            /*显示预览图*/
                            let input = document.createElement('input');
                            input.setAttribute('type', 'file');
                            input.setAttribute('accept', 'image/!*');
                            input.onchange = function () {
                                let file = this.files[0],
                                    reader = new FileReader();
                                reader.onload = function () {
                                    var id = 'blobid' + (new Date()).getTime();
                                    var blobCache = tinymce.activeEditor.editorUpload.blobCache;
                                    var base64 = reader.result.split(',')[1];
                                    var blobInfo = blobCache.create(id, file, base64);
                                    blobCache.add(blobInfo);
                                    cb(blobInfo.blobUri(), {title: file.name});//显示图片信息

                                    /*上传图片*/
                                    /*this.$axios.post('/api/upload', formdata)
                                        .then(res => {
                                            success(res.data.data.src)
                                        })
                                        .catch(res => {
                                            failure('error')
                                        })*/

                                };
                                reader.readAsDataURL(file);
                            };
                            input.click();

                        },
                        height: 500,
                        skin_url: 'static/tinymec/skins/lightgray',
                        plugins: 'link image emoticons',
                        toolbar: 'bold italic underline strikethrough | alignleft aligncenter alignright alignjustify | link unlink image emoticons',
                        branding: false
                    }
                },

            }
        },
        methods: {

            /*发布文章*/
            publish_article() {
                let cont = this.tinymce.html,
                    title = this.title,
                    blog_time = this.$utils.gen_date_time(),
                    add_part = this.$route.query.part || "blog",
                    add_info;
                console.log(cont);
                if (title) {
                    if (this.edit_mode === "add") {//新增文章

                        /*基于板块新增文章*/
                        if (add_part === "blog") {//新增学员博客
                            add_info = {
                                blog_name: title,
                                blog_body: cont,
                                blog_time: blog_time,
                            }
                        } else if (add_part === "discuss_msg_create") {//新增同学论坛
                            add_info = {
                                user: this.$store.state.user.uid,
                                title: title,
                                content: cont,
                                send_time: this.$utils.gen_date_time(),
                                click_num: 0,
                            }
                        }

                        this.$axios.post(add_part + "/", add_info).then(res => {
                            if (res.statusText == "Created") {
                                alert("文章创建成功！");
                                this.title = "";
                                this.tinymce.html = "";
                            }
                        })
                    } else if (this.edit_mode === "mod") {//修改文章
                        console.log("mod mode!")
                    }
                } else {
                    alert("请输入文章标题！");
                }
            },

            /*回退*/
            back() {
                this.$router.go(-1);
            }

        },
        mounted() {
            let article_title = this.$route.query.article_title;
            if (article_title) {
                this.edit_mode = "mod";//编辑模式（修改模式）
                this.$axios.get("blog/?search=" + article_title)
                    .then(res => {
                        let data = res.data[0];
                        this.title = data.blog_name;
                        this.tinymce.html = data.blog_body;
                    })
            }
        },
        components: {
            "tiny-editor": Editor
        }
    }
</script>

<style scoped>
    @import "../assets/css/pannel-widget.css";
    @import "../assets/css/base-title-widget.css";
    @import "../assets/css/seg-widget.css";
    @import "../assets/css/list-text-widget.css";
    @import "../assets/css/base-btn-widget.css";
    @import "../assets/css/main-title-widget.css";
</style>


