<template>
    <span :class="{'head-widget':true, 'h-w-green':color}">{{short_uname}}</span>
</template>

<script>
    export default {
        name: "head-widget",
        props: ["uname", "color"],
        computed: {

            /*显示名字最后2个字*/
            short_uname() {
                return this.uname.substr(-2, 2);
            },

        },
    }
</script>

<style scoped>
    @import "../../assets/css/head-widget.css";
</style>


