<template>
    <div class="login-widget">
        <div class="l-w-title">
            <p><img src="static/imgs/logo_simple.png"/></p>
            <p class="l-w-title-text"><span>格子网塾平台 - 用户登陆</span></p>
        </div>
        <div class="l-w-inp-item">
            <span class="icon-uname l-w-inp-item-ico"></span>
            <span class="l-w-inp-item-text">账号</span>
            <span class="l-w-inp-item-inp">
                <input type="text" placeholder="请输入账号" maxlength="16" v-model="login_info.uname" />
            </span>
        </div>
        <div class="l-w-inp-item">
            <span class="icon-upwd l-w-inp-item-ico"></span>
            <span class="l-w-inp-item-text">密码</span>
            <span class="l-w-inp-item-inp">
                <input type="password" placeholder="请输入密码" maxlength="12" v-model="login_info.upwd" />
            </span>
        </div>
        <div class="pad-h-2"></div>
        <div class="pcenter">
            <span state="normal" class="base-btn-widget" @click="user_login">登陆</span>
        </div>
    </div>
</template>

<script>
    export default {
        name: "login",
        data(){
            return {

                /*登录信息*/
                login_info: {
                    uname: "",
                    upwd: "",
                }

            }
        },
        methods: {

            /*登陆请求*/
            user_login(){
                this.$axios.post("login/", {
                    username: this.login_info.uname,
                    password: this.login_info.upwd,
                })
                    .then( res => {
                        let token = "JWT " + res.data.token;
                        this.$axios.defaults.headers.common['Authorization'] = token;
                        this.$store.commit("record_token", token);
                        this.$axios.get("userinfo/")
                            .then( sub_res => {
                                this.$store.commit("login", {
                                    uid: sub_res.data[0].id,
                                    uname: sub_res.data[0].name,
                                    name: this.login_info.uname,
                                    pwd: this.login_info.upwd
                                });
                                this.$router.push({path: this.$store.state.cur_route});//登录成功，跳转预备访问页/用户中心页
                            })
                            .catch( e => {
                                console.log(e);
                            })
                    })
            }

        }
    }
</script>

<style scoped>
    @import "../assets/css/login-widget.css";
    @import "../assets/css/base-btn-widget.css";
</style>


