<template>
    <div class="container">
        <h2 class="main-title-widget">
            <router-link class="mt-w-link" to="/learn_start?class_id=1">路线图</router-link>
            <span class="mt-w-text">{{cur_class.name}}</span>
        </h2>
        <div class="class-path-widget">
            <ul class="grid" v-if="cur_class.sub_class.length > 0">
                <li class="col-3 pad-right-1" v-for="(sub_class, index) of cur_class.sub_class">
                    <div class="cp-w-item" :state="sub_class.complete?'complete':''">
                        <router-link :to="'/learn_detail?sub_c_id=' + sub_class.sub_c_id">
                            <div class="cp-w-item-title">第<span class="cp-w-item-title-strong">{{index+1}}</span>课
                            </div>
                            <div class="cp-w-item-cont">
                                <h3 class="cp-w-item-sub-title">{{sub_class.name}}</h3>
                                <p>{{sub_class.desc}}</p>
                            </div>
                        </router-link>
                    </div>
                </li>
            </ul>
            <div class="pannel-widget" v-else>
                <div class="no-result-widget">
                    <span class="icon-nodata nr-w-ico"></span>
                    <span class="nr-w-text">暂无课程章节</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

    /*测试数据*/
    var cur_class = {//【data:当前课程】
        name: "Java",
        sub_class: [
            {
                sub_c_id: "a_1",
                name: "Java初识",
                desc: "对java进行简单介绍，Java程序如何执行，以及Java程序的结构。"
            },
            {
                sub_c_id: "a_2",
                name: "Java常量与变量",
                desc: "java的基本概念，包括标识符，关键字，数据类型，字面量，变量和常量。"
            },
            {
                sub_c_id: "a_3",
                name: "Java运算符",
                desc: "算术、赋值、逻辑和条件等常用运算符，以及简单的条件语句和 if-else。"
            },
            {
                sub_c_id: "a_4",
                name: "Java流程控制之选择结构",
                desc: "进一步学习选择结构，包括多重 if 与嵌套结构，以及 switch 语句。"
            },
            {
                sub_c_id: "a_5",
                name: "Java流程控制之循环结构",
                desc: "掌握 while 循环、do-while 循环、for 循环和循环嵌套的使用。"
            },
            {
                sub_c_id: "a_6",
                name: "Java数组之一维数组",
                desc: "介绍一维数组的定义和使用，以及增强型 for 循环的应用，并能够实现冒泡排序。"
            },
        ]
    }

    export default {
        name: "learn_path",
        data() {
            return {

                /*当前课程*/
                cur_class: {
                    name: "",
                    sub_class: []
                },

            }
        },
        mounted() {
            this.$axios.all([
                this.$axios.get("chapter/"),
                this.$axios.get("complete/")
            ]).then(this.$axios.spread((r1, r2) => {
                let sub_class = [];
                r1.data.forEach(data => {
                    if (data.course_name.id == this.$route.query.cid) {
                        let complete = false;

                        /*该章节是否完成*/
                        r2.data.forEach( comp => {
                            if(comp.chapter.id == data.id && comp.chapter_end === true){
                                complete = true;
                                return;
                            }
                        })

                        sub_class.push({
                            sub_c_id: data.id,
                            name: data.chapter_name,
                            desc: data.chapter_introduce,
                            complete: complete,
                        });
                    }
                })

                /*执行初始化*/
                this.cur_class = {
                    name: this.$route.query.cname,
                    sub_class: sub_class
                };
            }))
        }
    }
</script>

<style scoped>
    @import "../assets/css/main-title-widget.css";
    @import "../assets/css/class-path-widget.css";
    @import "../assets/css/no-result-widget.css";
    @import "../assets/css/pannel-widget.css";
</style>


