<template>
    <div>
        <tab-widget :tabs="tabs"></tab-widget>
        <editor-widget :mail-list="teachers"></editor-widget>
        <div class="pad-h-2" ></div>
    </div>
</template>

<script>
    import Tab from "@/components/widget/tab-widget";
    import Editor from "@/components/widget/editor-widget";

    /*测试数据*/
    var teachers = [//【data:老师通讯录】
        {
            uid: "u1",
            state: "",
            name: "铁林",
            suffix: "导师",
        },
        {
            uid: "u2",
            state: "",
            name: "宗盛",
            suffix: "讲师",
        },
        {
            uid: "u3",
            state: "",
            name: "王刚",
            suffix: "班主任",
        },
    ], history_msg = [//【data:历史消息】
        {
            history_msg_id: "history_msg_1",
            mission_id: "1",
            teacher: "张大千",
            date_time: "2018-10-10 23:20"
        }
    ];

    export default {
        name: "teacher",
        data() {
            return {

                /*tab-widget*/
                tabs: [
                    {
                        name: "老师",
                        state: "act",
                        type: "route",
                        link: "/coffee/teacher",
                    },
                    {
                        name: "同班",
                        type: "route",
                        link: "/coffee/same_class",
                    },
                    {
                        name: "同学",
                        type: "route",
                        link: "/coffee/student",
                    },
                ],

                /*通讯老师*/
                teachers: [],

            }
        },
        components: {
            "tab-widget": Tab,
            "editor-widget": Editor,
        },
        mounted() {
            this.$axios.get("user_class/")
                .then(r1 => {//初始化老师列表
                    let teachers = r1.data;
                    teachers.forEach(teacher => {
                        let th = teacher.teacher_head,       //导师
                            tl = teacher.teacher_lector,     //班主任
                            tt = teacher.teacher_tutor;      //讲师
                        this.teachers = [
                            {
                                uid: th.id,
                                state: "",
                                name: th.name,
                                suffix: "导师",
                            },
                            {
                                uid: tl.id,
                                state: "",
                                name: tl.name,
                                suffix: "班主任",
                            },
                            {
                                uid: tt.id,
                                state: "",
                                name: tt.name,
                                suffix: "讲师",
                            },
                        ]
                    })
                })
        },
    }
</script>

<style scoped>
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/basic-article-widget.css";
    @import "../../assets/css/base-btn-widget.css";
    @import "../../assets/css/head-widget.css";
</style>



