<template>
    <div>
        <tab-widget :tabs="tabs"></tab-widget>
        <div class="base-table-widget">
            <table class="bt-w-table">
                <thead>
                <tr class="bt-w-title">
                    <td class="bt-w-title-td">主题</td>
                    <td class="bt-w-title-td pcenter">用户</td>
                    <td class="bt-w-title-td pcenter">回复</td>
                    <td class="bt-w-title-td pcenter">浏览</td>
                    <td class="bt-w-title-td pcenter">活动</td>
                </tr>
                </thead>
                <tbody>
                <tr @click="check_msg" :id="msg.article_id" class="bt-w-tr" v-for="msg of msges">
                    <td class="bt-w-td">{{msg.title}}</td>
                    <td class="bt-w-td pcenter">
                        <head-widget :uname="msg.uname"></head-widget>
                    </td>
                    <td class="bt-w-td pcenter">{{msg.reply}}</td>
                    <td class="bt-w-td pcenter">{{msg.browse}}</td>
                    <td class="bt-w-td pcenter">{{msg.active}}</td>
                </tr>
                </tbody>
            </table>
            <div class="pad-h-3"></div>
            <div class="pcenter">
                <router-link to="/editor?part=discuss_msg_create" class="base-btn-widget">发帖</router-link>
            </div>
        </div>
    </div>
</template>

<script>
    import Tab from "@/components/widget/tab-widget";
    import Head from "@/components/widget/head-widget";

    /*测试数据*/
    var msges = [//【data: 最新论坛消息】
        {
            article_id: "article_1",
            title: "暂时只接受node.js相关问题",
            uname: "嘉诚",
            reply: 4,
            browse: 123,
            active: "2天",
        },
        {
            article_id: "article_2",
            title: "trupal 8 无法通过 url 安装模块",
            uname: "马云",
            reply: 12,
            browse: 783,
            active: "8小时",
        },
        {
            article_id: "article_3",
            title: "学习 trupal 网站开发，学习路径是怎样的？",
            uname: "建林",
            reply: 24,
            browse: 5452,
            active: "8小时",
        },
        {
            article_id: "article_4",
            title: "1025ma.com宁浩网学习2个月的成果",
            uname: "马云",
            reply: 102,
            browse: 23,
            active: "15小时",
        },
    ];

    export default {
        name: "student",
        data() {
            return {

                /*tab-widget*/
                tabs: [
                    {
                        name: "老师",
                        type: "route",
                        link: "/coffee/teacher",
                    },
                    {
                        name: "同班",
                        type: "route",
                        link: "/coffee/same_class",
                    },
                    {
                        name: "同学",
                        state: "act",
                        type: "route",
                        link: "/coffee/student",
                    },
                ],

                /*最新论坛消息*/
                msges: [],

            }
        },
        methods: {

            /*查看论坛消息*/
            check_msg(e){
                this.$router.push("/blog?article_id=" + e.currentTarget.id);
            }

        },
        components: {
            "tab-widget": Tab,
            "head-widget": Head,
        },
        mounted(){
            this.$axios.get("discuss_msg/")
                .then( res => {
                    let msges = res.data;
                    this.msges = this.$B.data_convert(msges, {
                        article_id: "id",
                        title: "title",
                        uname: (data)=>{
                            return data.user.name;
                        },
                        reply: (data)=>{
                            return data.discuss_replay.length;
                        },
                        browse: "click_num",
                        active: (data)=>{
                            return data.send_time.replace("T", " ");
                        },
                    })
                    this.msges.reverse();
                })
        }
    }
</script>

<style scoped>
    @import "../../assets/css/head-widget.css";
    @import "../../assets/css/chat-record-widget.css";
    @import "../../assets/css/base-table-widget.css";
    @import "../../assets/css/base-btn-widget.css";
</style>


