<template>
    <div>
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget">个人中心</h2>
                    <hr class="seg-widget"/>

                    <!--侧边栏导航组件-->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10">
                <div class="pannel-widget">
                    <div class="pad-h-1"></div>
                    <div class="pad-w-2 grid">
                        <div class="col-6">

                            <!--搜索栏组件-->
                            <div class="search-widget">
                                <div class="s-w-inp">
                                    <i class="s-w-inp-ico icon-sousuo"></i>
                                    <input v-model="search_work_name" class="s-w-inp-text" type="text"
                                           placeholder="搜索您的作业 ..."/>
                                </div>
                                <span class="s-w-btn" @click="search_work">搜索</span>
                            </div>

                        </div>
                    </div>
                    <div class="pad-h-2"></div>

                    <!--文件列表组件-->
                    <div class="base-table-widget" v-if="work_search_list.length > 0">
                        <table class="bt-w-table">
                            <thead class="bt-w-table">
                            <tr class="bt-w-title">
                                <td class="bt-w-title-td">文件名</td>
                                <td class="bt-w-title-td">发布日期</td>
                                <td class="bt-w-title-td pcenter">本地下载</td>
                            </tr>
                            </thead>
                            <tbody>
                            <tr class="bt-w-tr" v-for="work of work_search_list">
                                <td class="bt-w-td"><i class="icon-wenjian bt-w-ico-wenjian"></i>{{work.name}}</td>
                                <td class="bt-w-td">{{work.date}}</td>
                                <td class="bt-w-td pcenter"><a :href="work.download" download><i class="icon-xiazai"></i> 下载作业</a></td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span>
                        <span class="nr-w-text">暂无上传作业</span>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";

    /*测试数据*/
    var work_list = [//【data:学员作业】
            {
                work_id: "work_1",
                name: "开发项目源码",
                date: "2018-01-19 17:15",
                download: "",
            },
            {
                work_id: "work_2",
                name: "java就业项目-博客系统V1.0",
                date: "2018-01-19 11:12",
                download: "",
            },
            {
                work_id: "work_3",
                name: "摩拜单车爬虫源码",
                date: "2018-01-19 17:15",
                download: "",
            },
            {
                work_id: "work_4",
                name: "QNX系统",
                date: "2018-01-19 17:15",
                download: "",
            },
            {
                work_id: "work_5",
                name: "开发项目源码",
                date: "2018-01-19 17:15",
                download: "",
            }
        ];

    export default {
        name: "user_home_work",
        components: {
            "aside-nav-widget": AsideNav,
        },
        data() {
            return {

                /*aside_nav_widget*/
                aside_nav_data: [
                    {
                        name: "我的成绩",
                        type: "route",
                        link: "/user_home",
                    },
                    {
                        name: "我的作业",
                        act: "act",
                        type: "route",
                        link: "/user_home_work",
                    },
                    {
                        name: "我的博客",
                        type: "route",
                        link: "/user_home_blog",
                    },
                    {
                        name: "我的简历",
                        type: "route",
                        link: "/user_home_resume",
                    },
                    {
                        name: "设置",
                    },
                ],

                /*我的作业*/
                work_list: [],

                /*搜索作业*/
                work_search_list: [],

                /*搜索作业名*/
                search_work_name: "",

            }
        },
        methods: {

            /*作业搜索*/
            search_work() {
                this.$axios.get("task/?search=" + this.search_work_name)
                    .then( res => {
                        this.work_search_list = this.gen_work_data(res.data);
                    })
            },

            /*生成作业数据*/
            gen_work_data(data){
                return this.$B.data_convert(data, {
                    work_id: "id",
                    name: "task_name",
                    date: "complete_time",
                    download: "1"
                })
            }

        },
        mounted() {
            this.$axios.get("task/")
                .then(res => {
                    let data = res.data;
                    this.work_search_list = this.work_list = this.gen_work_data(data);
                })
        }
    }
</script>

<style scoped>
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/seg-widget.css";
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/base-table-widget.css";
    @import "../../assets/css/base-btn-widget.css";
    @import "../../assets/css/search-widget.css";
    @import "../../assets/css/no-result-widget.css";
</style>


