// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import axios from 'axios'
import App from './App'
import router from './router'
import store from './vuex/store'
import btx from './lib/Btx'

//axios.defaults.baseURL = "http://100ping.com/";//接口请求根路径
axios.defaults.baseURL = "http://47.92.92.207/";//接口请求根路径

Vue.config.productionTip = false
Vue.prototype.$axios = axios;
Vue.prototype.$B = btx;

/*全局工具方法*/
Vue.prototype.$utils = {

    /*合成发送时间*/
    gen_date_time(){
        let now = new Date();
        return now.toLocaleDateString().replace(/\//g, "-") + "T" + now.toLocaleTimeString().split("午")[1];
    }

};

/*http request 拦截器*/
axios.interceptors.request.use(
    config => {
        if (store.state.token) {//判断是否存在token，如果存在的话，则每个http header都加上token
            config.headers.Authorization = store.state.token;
        }
        return config;
    },
    err => {
        return Promise.reject(err);
    }
);

/*http response 拦截器*/
axios.interceptors.response.use(
    undefined,
    error => {
        let res = error.response;
        switch (res.status) {
            case 401:
                axios.post("login/", {
                    username: store.state.user.name,
                    password: store.state.user.pwd,
                }).then(res => {
                    let token = "JWT " + res.data.token;
                    axios.defaults.headers.common['Authorization'] = token;
                    store.commit("record_token", token);
                    router.replace({//返回首页
                        path: '/',
                    })
                })
                break;
            case 403:
                console.log('您没有该操作权限');
                break;
            case 500:
                console.log('服务器错误');
        }
        return Promise.reject(error.response.data);//返回接口返回的错误信息
    });

/*路由监测*/
router.beforeEach((to, from, next) => {
    if (to.meta.check_login == true) {//页面需要用户登录
        if (store.state.user.uid) {//已登陆
            next();
        } else {//未登陆

            /*缓存预备访问页*/
            if(to.path === "/learn_start"){
                store.commit("record_cur_route", "/user_home");
            }else{
                store.commit("record_cur_route", to.path);
            }

            next({path: "/login"});
        }
    } else {//页面无需用户登录
        next();
    }
})

new Vue({
    el: '#app',
    router,
    store,
    components: {App},
    template: '<App/>'
})



