<template>
    <div>
        <div>
            <div class="pad-h-2"></div>
            <div class="container grid">
                <div class="col-2 pad-right-2">
                    <div class="pannel-widget">
                        <h2 class="base-title-widget">竞技场</h2>
                        <hr class="seg-widget"/>

                        <!--侧边栏导航组件-->
                        <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                    </div>
                </div>
                <div class="col-10">
                    <div class="class-path-widget pad-w-2">
                        <h2 class="main-title-widget">{{cur_checkpoint.name}}
                            <small>（共计：{{cur_checkpoint.branch.length}} 关）</small>
                        </h2>
                        <ul class="grid">
                            <li class="col-3 pad-right-1" v-for="(branch, index) of cur_checkpoint.branch">
                                <div class="cp-w-item">
                                    <router-link :to="'/coding?id=' + branch.branch_id">
                                        <div class="cp-w-item-title">第<span
                                                class="cp-w-item-title-strong">{{index+1}}</span>关
                                        </div>
                                        <div class="cp-w-item-cont auto-h">
                                            <h3 class="cp-w-item-sub-title pcenter">{{branch.name}}</h3>

                                            <!--五星等级-->
                                            <p v-if="branch.rank >= 0" class="cp-w-item-stars grid">
                                                <span class="star-ico-widget icon-wuxing col-2d5"
                                                      v-for="star of branch.rank" rank="4"></span><!--实五星-->
                                                <span class="star-ico-widget icon-wuxing col-2d5"
                                                      v-for="star of (5 - branch.rank)"></span><!--空五星-->
                                            </p>
                                            <p v-else class="pcenter">
                                                <span class="lock-ico-widget">
                                                    <i class="icon-suo"></i>
                                                </span>
                                            </p>

                                        </div>
                                    </router-link>
                                </div>
                            </li>
                        </ul>
                    </div>
                    <div class="pad-h-5"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";

    /*测试数据*/
    var cur_checkpoint = {//【data:当前闯关关卡】
        name: "闯关赛",
        branch: [
            {
                branch_id: "branch_1",
                name: "初识Java",
                rank: 5,
            },
            {
                branch_id: "branch_2",
                name: "Java虚拟机",
                rank: 3,
            },
            {
                branch_id: "branch_3",
                name: "Java数据类型",
                rank: 0,
            },
            {
                branch_id: "branch_4",
                name: "Java表达式",
                rank: -1,
            },
            {
                branch_id: "branch_5",
                name: "Java分支结构",
                rank: -1,
            },
        ]
    };

    export default {
        name: "checkpoint_mode",
        data() {
            return {

                /*aside_nav_widget*/
                aside_nav_data: [
                    {
                        name: "排行榜",
                        type: "route",
                        link: "/arena_ranking_list",
                    },
                    {
                        name: "闯关模式",
                        act: "act",
                        type: "route",
                        link: "/arena_checkpoint_mode",
                    },
                    {
                        name: "对战模式",
                        type: "route",
                        link: "/arena_battle_mode",
                    },
                    {
                        name: "团赛模式",
                        type: "route",
                        link: "/arena_team_race_mode",
                    }
                ],

                /*当前关卡*/
                cur_checkpoint: {
                    name: "闯关赛",
                    branch: []
                }

            }
        },
        components: {
            "aside-nav-widget": AsideNav,
        },
        mounted() {
            this.$axios.all([
                this.$axios.get("pass/"),
                this.$axios.get("user_pass/")
            ]).then(this.$axios.spread((r1, r2) => {
                let passes = r1.data.results,
                    user_passes = r2.data.results;
                passes.forEach( pass => {
                    let rank = 0;

                    /*评分计算*/
                    user_passes.forEach( u_pass => {
                        if(u_pass.user_pass == pass.id){
                            rank = Math.min(Math.floor(u_pass.pass_score / 20) + 1, 5);
                        }
                    })

                    this.cur_checkpoint.branch.push({
                        branch_id: pass.id,
                        name: pass.name,
                        rank: rank,
                    });
                })
            }))
        }
    }
</script>

<style scoped>
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/seg-widget.css";
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/aside-nav-widget.css";
    @import "../../assets/css/main-title-widget.css";
    @import "../../assets/css/class-path-widget.css";
    @import "../../assets/css/star-ico-widget.css";
    @import "../../assets/css/lock-ico-widget.css";
</style>


