<template>
    <div>
        <tab-widget :tabs="tabs"></tab-widget>
        <editor-widget :mail-list="classmate"></editor-widget>
        <div class="pad-h-2"></div>
    </div>
</template>

<script>
    import Tab from "@/components/widget/tab-widget";
    import Editor from "@/components/widget/editor-widget";

    var classmate = [//【data:同学通讯录】
        {
            uid: "u1",
            state: "",
            name: "小白",
        },
        {
            uid: "u2",
            state: "",
            name: "林冲",
        },
        {
            uid: "u3",
            state: "",
            name: "马云",
        },
        {
            uid: "u4",
            name: "理想",
        },
        {
            uid: "u5",
            state: "",
            name: "冠杰",
        },
        {
            uid: "u6",
            state: "",
            name: "星驰",
        },
    ];

    export default {
        name: "same_class",
        data() {
            return {

                /*tab-widget*/
                tabs: [
                    {
                        name: "老师",
                        type: "route",
                        link: "/coffee/teacher",
                    },
                    {
                        name: "同班",
                        state: "act",
                        type: "route",
                        link: "/coffee/same_class",
                    },
                    {
                        name: "同学",
                        type: "route",
                        link: "/coffee/student",
                    },
                ],

                /*通讯同学*/
                classmate: [],

            }
        },
        components: {

            /*tab-widget*/
            "tab-widget": Tab,

            /*编辑器组件*/
            "editor-widget": Editor,

        },
        mounted(){
            this.$axios.get("classmate/")
                .then(res => {
                    let classmate = res.data;
                    this.classmate = this.$B.data_convert(classmate, {
                        uid: "id",
                        state: "",
                        name: "name",
                    });
                })
        },
    }
</script>

<style scoped>
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/basic-article-widget.css";
    @import "../../assets/css/base-btn-widget.css";
    @import "../../assets/css/chat-record-widget.css";
    @import "../../assets/css/head-widget.css";
</style>


