<template>
    <div>
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget">个人中心</h2>
                    <hr class="seg-widget"/>

                    <!--侧边栏导航组件-->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10">
                <div class="pannel-widget">
                    <div class="pad-h-1"></div>
                    <div class="pad-w-2">
                        <router-link to="/editor" class="base-btn-widget">发布博客</router-link>
                    </div>
                    <div class="pad-h-2"></div>

                    <!--文件列表组件-->
                    <div class="base-table-widget" v-if="blogs.length > 0">
                        <table class="bt-w-table">
                            <thead class="bt-w-table">
                            <tr class="bt-w-title">
                                <td class="bt-w-title-td">标题</td>
                                <td class="bt-w-title-td pcenter">发布时间</td>
                                <td class="bt-w-title-td pcenter">操作</td>
                            </tr>
                            </thead>
                            <tbody>
                            <tr class="bt-w-tr" v-for="blog of blogs">
                                <td class="bt-w-td">{{blog.title}}</td>
                                <td class="bt-w-td pcenter">{{blog.date_time}}</td>
                                <td class="bt-w-td pcenter"><router-link :to="'/editor?article_title=' + blog.title"><i class="icon-bianji"></i> 编辑</router-link></td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span>
                        <span class="nr-w-text">暂无博客</span>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";

    var blogs = [//【data: 我的博客】
        {
            blog_id: "blog_1",
            title: "this is a test",
            date_time: "2018-8-10 14:20"
        },
        {
            blog_id: "blog_2",
            title: "this is a title",
            date_time: "2018-6-21 14:10"
        },
    ];

    export default {
        name: "user_home_blog",
        data() {
            return {

                /*aside_nav_widget*/
                aside_nav_data: [
                    {
                        name: "我的成绩",
                        type: "route",
                        link: "/user_home",
                    },
                    {
                        name: "我的作业",
                        type: "route",
                        link: "/user_home_work",
                    },
                    {
                        name: "我的博客",
                        act: "act",
                        type: "route",
                        link: "/user_home_blog",
                    },
                    {
                        name: "我的简历",
                        type: "route",
                        link: "/user_home_resume",
                    },
                    {
                        name: "设置",
                    },
                ],

                /*博客信息*/
                blogs: [],

            }
        },
        components: {
            "aside-nav-widget": AsideNav,
        },
        mounted() {
            this.$axios.get("blog/")
                .then(res => {
                    this.blogs = this.$B.data_convert(res.data, {
                        blog_id: "id",
                        title: "blog_name",
                        date_time: "blog_time"
                    }).reverse();
                })
        }
    }
</script>

<style scoped>
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/seg-widget.css";
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/base-table-widget.css";
    @import "../../assets/css/base-btn-widget.css";
    @import "../../assets/css/no-result-widget.css";
</style>


