<template>
    <div class="nav-widget">
        <div class="container n-w-box grid">
            <div class="n-w-left">
                <img class="n-w-logo" src="static/imgs/logo.png"/>
            </div>
            <ul class="n-w-list">
                <li class="n-w-item" v-for="nav of navs">
                    <router-link class="n-w-text" :to="nav.to" :state="nav.state">{{nav.name}}</router-link>
                </li>
            </ul>
            <div class="n-w-right">
                <span v-if="user.uid==''">
                    <router-link to="/user_home" state="login" class="head-widget">
                        <span class="icon-login h-w-login-ico"></span>
                    </router-link>
                    <router-link to="/user_home" >登录</router-link>
                </span>
                <span v-else>
                    <router-link to="/user_home">
                        <head-widget :uname="user.uname"></head-widget>
                    </router-link>
                    <a @click="exit_login">退出</a>
                </span>
            </div>
        </div>
    </div>
</template>

<script>
    import Head from "@/components/widget/head-widget"

    /*当前导航选中状态*/
    function nav_act(navs, path){
        navs.forEach(function (nav) {
            if (nav.state === "act") nav.state = "";
            if (nav.to.split("/")[1] === path.split("/")[1]) {
                nav.state = "act";
            }
        })
    }

    export default {
        name: 'nav-widget',
        data() {
            return {

                /*主导航*/
                navs: [
                    {
                        name: "路线图",
                        to: "/",
                        state: "",
                    },
                    {
                        name: "图书馆",
                        to: "/library",
                        state: "",
                    },
                    {
                        name: "竞技场",
                        to: "/arena_ranking_list",
                        state: "",
                    },
                    {
                        name: "咖啡厅",
                        to: "/coffee/teacher",
                        state: "",
                    },
                ],

            }
        },
        methods: {

            /*退出登录*/
            exit_login(){
                this.$store.commit("exit_login");
                this.$router.push("/");
            }

        },
        computed: {

            /*用户信息*/
            user() {
                return this.$store.state.user;
            }

        },
        beforeMount(){
            nav_act(this.navs, this.$router.history.current.fullPath);
        },
        watch: {
            $route(to) {
                nav_act(this.navs, to.path);
            }
        },
        components: {
            "head-widget": Head,
        }
    }
</script>

<style scoped>
    @import "../../assets/css/nav-widget.css";
    @import "../../assets/css/head-widget.css";
</style>


