<template>
    <div class="container">
        <h2 class="main-title-widget">
            <router-link class="mt-w-link"
                         :to="'/learn_path?cid=' + cur_sub_class.course_id + '&cname=' + cur_sub_class.name">
                {{cur_sub_class.name}}
            </router-link>
            <span class="mt-w-text">{{class_detail.task.title}}</span>
        </h2>
        <div class="pannel-widget">
            <div class="pad-h-1"></div>
            <div class="grid">
                <div class="col-4 pad-w-2">
                    <div class="video-widget">
                        <video controls>
                            <source :src="'/static/res/video/' + class_detail.task.video"
                                    type="video/mpeg"/>
                        </video>
                    </div>
                </div>
                <div class="col-8 pad-w-2">
                    <h2>{{class_detail.task.title}}</h2>
                    <div class="basic-article-widget">
                        <h3 class="ba-w-title">本课目标：</h3>
                        <div style="font-size: 1.3rem" v-html="class_detail.task.purpose"></div>
                    </div>
                    <div><span class="base-btn-widget" @click="send_work" :state="btn_state.state">{{btn_state.text}}</span></div>
                    <div class="pad-h-5 pright">
                        <router-link :to="'/library?search=' + class_detail.task.search">>
                            去图书馆搜索“{{class_detail.task.search}}”
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
        <div class="pannel-widget" style="margin-top: 1rem">
            <tab-widget :tabs="tabs"></tab-widget>
        </div>
        <div v-if="cur_tasks.length > 0">
            <div style="margin-top: 1rem" v-for="(task, index) of cur_tasks">
                <div class="pannel-widget grid">

                    <!--作业任务-->
                    <div class="basic-article-widget col-8">
                        <div class="ba-w-p pad-w-2">
                            <p>【{{index + 1}}. {{task.name}}】 {{task.desc}}</p>
                            <div class="ba-w-p-imgs">
                                <img class="ba-w-p-img" :src="'http://' + img" v-for="img of task.images"/>
                                <p>
                                <span state="visied" class="base-btn-widget" @click="upload"><input type="file"
                                                                                                    v-show="false"
                                                                                                    @change="exe_upload"/>上传作业</span>
                                </p>
                            </div>
                        </div>
                    </div>

                    <!--作业点评-->
                    <div class="col-4">
                        <h3 class="base-title-widget bt-w-blue">作业点评</h3>
                        <hr class="seg-widget"/>
                        <div v-if="task.msgs.length > 0">
                            <div class="pad-w-2" v-for="(msg, i) of task.msgs">
                                <div class="basic-article-widget">
                                    <p class="ba-w-p">{{msg.text}}</p>
                                    <p class="ba-w-small-p pright">
                                        <span class="text-strong">{{msg.date_time}}</span>
                                        <head-widget :uname="msg.teacher"></head-widget>
                                    </p>
                                </div>
                                <hr class="seg-widget" state="max_line" v-if="task.msgs.length-1 > i"/>
                            </div>
                        </div>
                        <div class="no-result-widget" v-else>
                            <span class="icon-nodata nr-w-ico"></span>
                            <span class="nr-w-text">暂无点评信息</span>
                        </div>
                    </div>

                </div>
            </div>
        </div>
        <div class="pannel-widget no-result-widget" style="margin-top: 1rem" v-else>
            <span class="icon-nodata nr-w-ico"></span>
            <span class="nr-w-text">暂无内容</span>
        </div>
        <div class="pad-h-5"></div>
    </div>
</template>

<script>
    import $B from "@/lib/Btx";
    import Tab from "@/components/widget/tab-widget";
    import Head from "@/components/widget/head-widget";

    /*测试数据*/
    var cur_sub_class = {//【data:课程介绍】
            course_id: 1,
            chapter_id: 1,
            name: "Java初识"
        },
        class_detail = {//【data:课程详情】
            task: {
                task_id: "task_1",
                type: "TASK_OPERATE",
                video: "res_video_1.mp4",
                title: "编写Helloworld程序",
                purpose: "111Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean euismod bibendum laoreet. Proin gravida dolor sit amet lacus accumsan et viverra justo commodo. Proin sodales pulvinar\ntempor. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nam fermentum, nulla luctus pharetra vulputate.",
                search: "Java配置环境",
                tasks: [
                    {
                        id: 2,
                        desc: "111Lorem ipsum dolor sit amet",
                        images: ["t.jpg"],
                        msgs: [
                            {
                                text: "作业完成的很不错",
                                teacher: "马云",
                                date_time: "2018-6-5 13:34:10"
                            }
                        ]
                    }
                ]
            }
        }

    export default {
        name: "learn_detail",
        data() {
            let vue = this;
            return {

                /*课程介绍*/
                cur_sub_class: {},

                /*按钮状态*/
                btn_state: {
                    text: "完成任务",
                    state: "normal"
                },

                /*课程详情*/
                class_detail: {
                    task: {
                        tasks: []
                    },
                    practice: []
                },

                /*tab_widget*/
                tabs: [
                    {
                        name: "任务",
                        state: "act"
                    },
                    {
                        name: "练习",
                        state: "",
                        callback() {
                            vue.$router.push("/question?cid=" + vue.cur_sub_class.chapter_id);
                        }
                    }
                ],

            }
        },
        computed: {

            /*当前显示任务/练习*/
            cur_tasks() {
                return this.class_detail.task.tasks;
            },

        },
        methods: {

            /*提交作业*/
            send_work() {
                this.btn_state = {
                    text: "已提交",
                    state: "visied"
                };
                //发送服务端相关数据
            },

            /*选择任务或练习*/
            sel_tab(e) {
                $B.select("p-w-tab", e.target);
            },

            /*点击上传作业*/
            upload(e) {
                console.log(e.target);
                e.target.querySelector("input[type='file']").click();
            },

            /*上传执行*/
            exe_upload(e) {
                let files = e.target.files,
                    file = files[0],
                    formdata = new FormData();
                formdata.append('fileSelect', file);
                console.log(file);
            },

        },
        mounted() {
            this.$axios.all([
                this.$axios.get("chapter/" + this.$route.query.sub_c_id + "/"),
                this.$axios.get("evaluation/")
            ]).then(this.$axios.spread((r1, r2) => {
                let cdata = r1.data;

                /*课程介绍*/
                this.cur_sub_class = {
                    name: cdata.course_name.name,
                    course_id: cdata.course_name.id,
                    chapter_id: cdata.id,
                }

                this.$axios.get("chaptertask/")
                    .then(t_res => {
                        let tdata = t_res.data,
                            cid = cdata.id,
                            tasks = [];

                        /*任务*/
                        tdata.forEach(data => {
                            if (cid == data.chapter_name) {

                                /*评论消息列表*/
                                let msgs = r2.data,
                                    mission_msgs = [];
                                msgs.forEach(msg => {
                                    if (msg.user === this.$store.state.user.uid && data.id === msg.mission) {
                                        mission_msgs.push({
                                            text: msg.data,
                                            teacher: msg.teacher.name,
                                            date_time: msg.evaluation_time.replace("T", " ")
                                        });
                                    }
                                });
                                mission_msgs.reverse();

                                tasks.push({
                                    id: data.id,
                                    desc: data.info,
                                    name: data.name,
                                    images: data.image,
                                    msgs: mission_msgs
                                })
                            }
                        })

                        /*课程详情*/
                        this.class_detail = {
                            task: {
                                task_id: cdata.id,
                                type: "TASK_OPERATE",
                                video: cdata.chapter_video || "video_2.mp4",
                                title: cdata.chapter_name,
                                purpose: cdata.chapter_task,
                                search: cdata.chapter_target,
                                tasks
                            }
                        }

                    })
            }))
        },
        components: {
            "tab-widget": Tab,
            "head-widget": Head,
        }
    }
</script>

<style scoped>
    @import "../assets/css/main-title-widget.css";
    @import "../assets/css/pannel-widget.css";
    @import "../assets/css/video-widget.css";
    @import "../assets/css/base-btn-widget.css";
    @import "../assets/css/basic-article-widget.css";
    @import "../assets/css/base-title-widget.css";
    @import "../assets/css/no-result-widget.css";
    @import "../assets/css/seg-widget.css";
</style>


