<template>
    <div class="learn-outline-widget">
        <dl class="lo-w-list pannel-widget" v-for="(item, index) of class_info.list">
            <dt class="base-title-widget"><h3>{{item.title}}</h3></dt>
            <dd class="grid lo-w-list-items">
                <div class="col-3" :id="data.id" :data-name="data.name" :data-desc="data.desc" v-for="data of item.datas" @click="sel_class">
                    <div :rank="data.rank" :select="data.select" class="lo-w-list-item">
                        <img class="lo-w-list-item-cover" :src="data.cover?data.cover:'static/imgs/class/banner/f.jpg'" />
                        <div class="lo-w-list-item-name"><i v-if="data.rank>1" class="star-ico-widget icon-wuxing lo-w-list-item-ico" :rank="data.rank"></i>{{data.name}}</div>
                    </div>
                </div>
            </dd>
        </dl>
    </div>
</template>

<script>
    export default {
        name: "learn-outline-widget",
        props: ["class_info"],
        methods: {

            /*传递所选课程*/
            sel_class(e){
                let $btn = e.currentTarget,
                    datas = $btn.dataset;

                this.$emit("sel_class", {
                    id: $btn.id,
                    name: datas.name,
                    desc: datas.desc,
                })
            }

        }
    }
</script>

<style scoped>
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/learn-outline-widget.css";
    @import "../../assets/css/star-ico-widget.css";
</style>


