<template>
    <div>
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget">竞技场</h2>
                    <hr class="seg-widget"/>

                    <!--侧边栏导航组件-->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10">

                <!--搜索栏组件-->
                <div class="pannel-widget pad-w-2">
                    <div class="search-widget">
                        <div class="s-w-inp">
                            <i class="s-w-inp-ico icon-sousuo"></i>
                            <input class="s-w-inp-text" type="text" v-model="search_text"
                                   placeholder="搜索用户ID ..."/>
                        </div>
                        <span class="s-w-btn" @click="search">搜索</span>
                    </div>
                </div>

                <div class="pad-h-1"></div>

                <div class="pannel-widget">
                    <tab-widget :tabs="tabs"></tab-widget>

                    <!--向我挑战-->
                    <h3 class="base-title-widget bt-w-blue">向我挑战</h3>
                    <hr class="seg-widget"/>
                    <div class="pad-h-1"></div>
                    <div class="base-table-widget" v-if="challenger.length > 0">
                        <table class="bt-w-table">
                            <thead>
                            <tr class="bt-w-title">
                                <td class="bt-w-title-td">学员</td>
                                <td class="bt-w-title-td pcenter">等级</td>
                                <td class="bt-w-title-td pcenter">金币</td>
                                <td class="bt-w-title-td pcenter">操作</td>
                            </tr>
                            </thead>
                            <tbody @click="operate_challenge">
                            <tr class="bt-w-tr" v-for="user of challenger">
                                <td class="bt-w-td">
                                    <head-widget :uname="user.name"></head-widget> <span>{{user.name}}</span>
                                </td>
                                <td class="bt-w-td pcenter">{{user.level}} 级</td>
                                <td class="bt-w-td pcenter">{{user.coin}} 枚</td>
                                <td class="bt-w-td pcenter"><router-link to="/typing" class="bt-w-btn" :challenge-id="user.challenge_id">迎战</router-link> <span
                                        class="bt-w-btn" :void-challenge="user.challenge_id">回避</span></td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span><span class="nr-w-text">暂无挑战</span>
                    </div>
                    <div class="pad-h-2"></div>

                    <!--我要挑战-->
                    <h3 class="base-title-widget bt-w-blue">我要挑战</h3>
                    <hr class="seg-widget"/>
                    <div class="pad-h-1"></div>
                    <div class="base-table-widget" v-if="fighter.length > 0">
                        <table class="bt-w-table">
                            <thead>
                            <tr class="bt-w-title">
                                <td class="bt-w-title-td">学员</td>
                                <td class="bt-w-title-td pcenter">等级</td>
                                <td class="bt-w-title-td pcenter">金币</td>
                                <td class="bt-w-title-td pcenter">操作</td>
                            </tr>
                            </thead>
                            <tbody @click="operate_challenge">
                            <tr class="bt-w-tr" v-for="(user, index) of fighter">
                                <td class="bt-w-td">
                                    <head-widget :uname="user.name"></head-widget> <span>{{user.name}}</span>
                                </td>
                                <td class="bt-w-td pcenter">{{user.level}} 级</td>
                                <td class="bt-w-td pcenter">{{user.coin}} 枚</td>
                                <td class="bt-w-td pcenter"><router-link to="/typing" class="bt-w-btn" :challenge-id="user.fight_id">迎战</router-link></td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span><span class="nr-w-text">暂无挑战</span>
                    </div>

                </div>
                <div class="pad-h-5"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";
    import Tab from "@/components/widget/tab-widget";
    import HeadWidget from "@/components/widget/head-widget";

    export default {
        name: "battle_mode",
        data() {
            var vue = this,
                aside_nav_data = [
                {
                    name: "排行榜",
                    type: "route",
                    link: "/arena_ranking_list",
                },
                {
                    name: "闯关模式",
                    type: "route",
                    link: "/arena_checkpoint_mode",
                },
                {
                    name: "对战模式",
                    act: "act",
                    type: "route",
                    link: "/arena_battle_mode",
                },
                {
                    name: "团赛模式",
                    type: "route",
                    link: "/arena_team_race_mode",
                }
            ], tabs = [
                {
                    name: "时间赛",
                    state: "act",
                    callback() {
                        vue.challenger = challenger;//????拉数据-challenger&fighter
                        vue.fighter = fighter;
                    }
                },
                {
                    name: "速度赛",
                    state: "",
                    callback() {
                        vue.challenger = [];//????拉数据-challenger&fighter
                    }
                },
                {
                    name: "编程赛",
                    state: "",
                    callback() {
                        vue.fighter = [];//????拉数据-challenger&fighter
                    }
                },
            ], challenger = [//【data:向我挑战（挑战者）】
                {
                    challenge_id: "challenge_1",
                    uid: "u3",
                    name: "李嘉诚",
                    level: "3",
                    coin: "120",
                }
            ], fighter = [//【data:我要挑战（斗士）】
                {
                    fight_id: "fight_1",
                    uid: "u4",
                    name: "马云",
                    level: "13",
                    coin: "110",
                },
                {
                    fight_id: "fight_2",
                    uid: "u5",
                    name: "李彦宏",
                    level: "6",
                    coin: "80",
                },
                {
                    fight_id: "fight_3",
                    uid: "u6",
                    name: "马化腾",
                    level: "31",
                    coin: "642",
                },
                {
                    fight_id: "fight_4",
                    uid: "u7",
                    name: "雷军",
                    level: "19",
                    coin: "171",
                },
            ];

            return {

                /*aside_nav_widget*/
                aside_nav_data,

                /*tab_widget*/
                tabs,

                /*搜索id字段*/
                search_text: "",

                /*向我挑战*/
                challenger,

                /*我要挑战*/
                fighter,

            }
        },
        methods: {

            /*搜索用户ID-对应的挑战????*/
            search() {
            },

            /*向我挑战-操作*/
            operate_challenge(e) {
                let tag = e.target,
                    challenge_id,
                    challenger = this.challenger;

                /*回避挑战*/
                if (challenge_id = tag.getAttribute("void-challenge")) {
                    challenger.forEach(function (challenge, index) {
                        if (challenge.challenge_id === challenge_id) {
                            challenger.splice(index, 1);
                        }
                    })
                    //????发送服务端清除挑战数据-challenge_id
                }

                /*接受挑战*/
                if (challenge_id = tag.getAttribute("challenge-id")) {
                    console.log(challenge_id);
                    //????路由跳转对战页面
                }

            }

        },
        components: {
            "head-widget": HeadWidget,
            "tab-widget": Tab,
            "aside-nav-widget": AsideNav,
        }
    }
</script>

<style scoped>
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/seg-widget.css";
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/aside-nav-widget.css";
    @import "../../assets/css/base-table-widget.css";
    @import "../../assets/css/main-title-widget.css";
    @import "../../assets/css/search-widget.css";
    @import "../../assets/css/no-result-widget.css";
</style>


