import Vue from "vue";
import Vuex from "vuex";
import createPersistedstate from "vuex-persistedstate";

Vue.use(Vuex);

export default new Vuex.Store({
    plugins: [
        createPersistedstate()
    ],
    state: {
        user: {
            uid: "",
            uname: "",
            name: "",
            pwd: "",
        },
        cur_route: "",
        token: ""
    },
    mutations: {

        /*记录token*/
        record_token(state, val){
            state.token = val;
        },

        /*记录登陆落地页*/
        record_cur_route(state, val){
            state.cur_route = val;
        },

        /*用户登录*/
        login(state, val) {
            state.user = val;
        },

        /*退出登录*/
        exit_login(state){
            state.user.uid = "";
            state.user.uname = "";
            state.user.pwd = "";
        },

        /*修改用户名*/
        update_uname(state, uname){
            state.user.uname = uname;
        },

    }
})


