import Vue from 'vue';
import Router from 'vue-router';

//登陆页
import login from '@/components/login';

//首页
import home from '@/components/home';

//路线图
import learn_start from '@/components/learn_start';
import learn_path from '@/components/learn_path';
import learn_detail from '@/components/learn_detail';

//图书馆
import library from '@/components/library';

//竞技场
import ranking_list from '@/components/arena/ranking_list';
import checkpoint_mode from '@/components/arena/checkpoint_mode';
import battle_mode from '@/components/arena/battle_mode';
import team_race_mode from '@/components/arena/team_race_mode';

//咖啡厅
import coffee from '@/components/coffee';
import teacher from '@/components/coffee/teacher';
import same_class from '@/components/coffee/same_class';
import student from '@/components/coffee/student';

//我的（个人中心）
import user_home from '@/components/user/user_home';
import user_home_work from '@/components/user/user_home_work';
import user_home_blog from '@/components/user/user_home_blog';
import user_home_resume from '@/components/user/user_home_resume';

//题型
import question from '@/components/question';
import coding from '@/components/coding';
import typing from '@/components/typing';

//文章
import blog from '@/components/blog';

//编辑器
import editor from '@/components/editor';

Vue.use(Router)

const router = new Router({
    routes: [

        //home
        {
            path: '/',
            component: home
        },

        //login
        {
            path: '/login',
            component: login
        },

        {
            path: '/learn_start',
            component: learn_start,
            meta: {
                check_login: true
            }
        },
        {
            path: '/learn_path',
            component: learn_path,
            meta: {
                check_login: true
            }
        },
        {
            path: '/learn_detail',
            component: learn_detail,
            meta: {
                check_login: true
            }
        },

        //图书馆
        {
            path: '/library',
            component: library
        },

        //竞技场
        {
            path: '/arena_ranking_list',
            component: ranking_list,
            meta: {
                check_login: true
            }
        },
        {
            path: '/arena_checkpoint_mode',
            component: checkpoint_mode,
            meta: {
                check_login: true
            }
        },
        {
            path: '/arena_battle_mode',
            component: battle_mode,
            meta: {
                check_login: true
            }
        },
        {
            path: '/arena_team_race_mode',
            component: team_race_mode,
            meta: {
                check_login: true
            }
        },

        //咖啡厅
        {
            path: '/coffee',
            component: coffee,
            meta: {
                check_login: true
            },
            children: [
                {
                    path: 'teacher',
                    component: teacher,
                    meta: {
                        check_login: true
                    }
                },
                {
                    path: 'same_class',
                    component: same_class,
                    meta: {
                        check_login: true
                    }
                },
                {
                    path: 'student',
                    component: student,
                    meta: {
                        check_login: true
                    }
                },
            ]
        },

        //个人中心
        {
            path: '/user_home',
            component: user_home,
            meta: {
                check_login: true
            }
        },
        {
            path: '/user_home_work',
            component: user_home_work,
            meta: {
                check_login: true
            }
        },
        {
            path: '/user_home_blog',
            component: user_home_blog,
            meta: {
                check_login: true
            }
        },
        {
            path: '/user_home_resume',
            component: user_home_resume,
            meta: {
                check_login: true
            }
        },

        //题型
        {
            path: '/question',
            component: question,
            meta: {
                check_login: true
            }
        },
        {
            path: '/coding',
            component: coding,
            meta: {
                check_login: true
            }
        },
        {
            path: '/typing',
            component: typing,
            meta: {
                check_login: true
            }
        },

        //文章
        {
            path: '/blog',
            component: blog,
            meta: {
                check_login: true
            }
        },

        //编辑器
        {
            path: '/editor',
            component: editor,
            meta: {
                check_login: true
            }
        },

    ]
})

export default router



