<template>
    <div class="container">
        <h2 class="main-title-widget">
            <a class="mt-w-link" @click="back">返回章节任务</a>
        </h2>
        <div class="grid">
            <div class="col-8">
                <div class="pannel-widget">
                    <div v-if="working">
                        <div class="pad-h-2"></div>
                        <div class="pad-left-2 pcenter">
                            <span :state="test_btn.state" class="base-btn-widget" @click="start_test">{{test_btn.text}}</span>
                        </div>
                        <div class="pad-h-4"></div>
                    </div>

                    <!--答题组件-->
                    <div class="question-widget" v-if="questions.length > 0" :id="cur_question.question_id" v-show="test_btn.test_state === 'do_test'">
                        <div class="q-w-type">{{cur_question_number}}. {{cur_question.type_text}}</div>
                        <div class="q-w-desc" v-html="cur_question.text" @input="typin_answer"></div>
                        <ul class="q-w-items" v-if="cur_question.item" @click="cho_answer">
                            <li v-for="(item, index) of cur_question.item" class="q-w-item"
                                :state="cur_question.type">

                                <!--判断题-->
                                <span v-if="cur_question.type == 'jud'">
                                    <input type="radio" :name="cur_question.question_id"
                                           :value="index + 1" v-model="cur_question.val"
                                           class="q-w-sel-btn"/>
                                    <span class="q-w-sel-text">{{item}}</span>
                                </span>

                                <!--选择题-->
                                <span v-else>
                                    <input :type="cur_question.type == 'cho'? 'radio': 'checkbox'"
                                           :name="cur_question.question_id"
                                           :value="index + 1" v-model="cur_question.val"
                                           class="q-w-sel-btn"/>
                                    <span class="q-w-sel-text">{{item}}</span>
                                </span>

                            </li>
                        </ul>
                        <div class="q-w-btns">
                            <span class="q-w-btn" @click="toggle_question(-1)"
                                  :state="cur_question_number == 1?'disabled':''">上一题</span> | <span
                                class="q-w-btn" @click="toggle_question('1')"
                                :state="cur_question_number == questions.length?'disabled':''">下一题</span>
                        </div>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span>
                        <span class="nr-w-text" ref="notic">准备开始 !</span>
                    </div>

                    <!--试卷组件-->
                    <div class="test-paper-widget" v-if="questions.length > 0" v-show="test_btn.test_state === 'check_test'">
                        <ul>
                            <li class="tp-w-answer" :data-id="question.question_id" v-for="(question, index) of questions">
                                <div class="tp-w-type">{{index + 1}}. {{question.type_text}}</div>
                                <div class="tp-w-desc" v-html="question.text"></div>
                                <!--选择题选项-->
                                <ul class="tp-w-choes" v-if="question.item && question.type == 'cho'">
                                    <li v-for="(item, _index) of question.item" class="tp-w-item"
                                        :state="question.val == _index + 1? 'act': ''">
                                        <span class="tp-w-sel-text">{{item}}</span>
                                    </li>
                                </ul>
                                <div class="tp-w-sel-result" v-if="question.standard_answer" v-html="question.standard_answer"></div>
                            </li>
                        </ul>
                    </div>

                </div>
            </div>
            <div class="col-4 pad-left-2">
                <div v-if="checkpoint.name">
                    <div class="pannel-widget">
                        <h3 class="base-title-widget bt-w-blue">{{checkpoint.name}}</h3>
                        <hr class="seg-widget"/>
                        <div class="basic-article-widget pad-w-2">
                            <p class="ba-w-p">{{checkpoint.desc}}</p>
                        </div>
                    </div>
                    <div class="pad-h-1"></div>
                </div>

                <div class="pannel-widget">
                    <h3 class="base-title-widget bt-w-blue">测试信息</h3>
                    <div class="list-text-widget grid pad-w-2">
                        <div class="col-12">
                            <div class="lt-w-item">
                                <span>题目数</span>
                                <span class="lt-w-item-after">{{questions.length}} 题</span>
                            </div>
                            <div class="lt-w-item">
                                <span>{{cur_time.time}}</span>
                                <span class="lt-w-item-after" v-if="checkpoint.time_limit">{{checkpoint.time_limit_format}} 分钟</span>
                            </div>
                        </div>
                    </div>
                    <div class="pcenter" v-if="test_btn.state == 'visied'">
                        <div class="pad-h-2"></div>
                        <span class="base-btn-widget" @click="send_test" v-if="working">提交测试</span>
                        <div class="pad-h-1"></div>
                    </div>
                </div>
            </div>
        </div>

        <div class="pad-h-4"></div>
    </div>
</template>

<script>

    /*测试数据*/
    var questions = [//【data:试题集】
            {
                question_id: "question_1",
                type: "cho",
                text: "假设今天是2006年4月1日星期六,请问以下javascript代码输出结果是",
                item: ["2006", "4", "1"],
            },
            {
                question_id: "question_5",
                type: "cho",
                text: "在jQuery中需要选取&lt;p&gt;元素里所有&lt;a&gt;元素，则下列选择器写法正确的是",
                item: ["$(\"p a\")", "$(\"p+ a\")", "$(\"p>a\")", "$(\"p~a\")"],
            },
            {
                question_id: "question_2",
                type: "com",
                text: "在HTML文档对象模型(DOM)中,history对象的(?)方法用于加载历史列表中的下一个URL页面。",
            },
            {
                question_id: "question_4",
                type: "jud",
                text: "go(-1)可以使窗口显示前一个页面",
            },
        ],
        pad_zero = val => {
            return ("" + val).length == 1 ? "0" + val : val;
        };

    export default {
        name: "question",
        data() {
            var checkpoint = {//【data:关卡介绍】
                checkpoint_id: "checkpoint_1",
                time_limit: "60000",
                desc: "111Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean euismod bibendum laoreet. Proin gravida dolor sit amet lacus accumsan et viverra justo commodo. Proin sodales pulvinar\ntempor. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nam fermentum, nulla luctus pharetra vulputate."
            };

            /*关卡介绍加工*/
            if (checkpoint.time_limit) {
                checkpoint.name = "限时赛";
                checkpoint.time_limit_format = checkpoint.time_limit / 1000 / 60;
            }

            return {

                /*关卡介绍*/
                checkpoint: {
                    checkpoint_id: "",
                    time_limit: "",
                    desc: ""
                },

                /*试题集*/
                pre_questions: [],
                questions: [],

                /*当前试题序号*/
                cur_question_number: 1,

                /*做题按钮*/
                test_btn: {
                    text: "开始测试",
                    state: "normal",
                    test_state: "do_test",
                },

                /*起始时间*/
                cur_time: {
                    flag: null,
                    time: "00:00:00",
                    es_time: 0,
                },

                /*答案选项*/
                answers: {
                    cho: ["A", "B", "C", "D", "E", "F", "G"],//选择题
                    jud: ["√", "×"],//判断题
                },

                /*答题状态*/
                working: true,

                /*答案集*/
                standard_answers: {},

            }
        },
        methods: {

            /*开始测试*/
            start_test() {
                if (this.test_btn.state === "normal") {//开始测试
                    this.questions = this.pre_questions;

                    /*无试题提示*/
                    if(this.questions.length == 0){
                        this.$refs.notic.innerHTML = "暂无试题 !";
                        return;
                    }
                    this.test_btn.state = "visied";
                    this.test_btn.text = "检查试卷";

                    /*实时显示时间*/
                    let pad_s = "<span style='padding: 0 .7rem;'>(<span style='display: inline-block; padding: 0 .4rem; font-weight: bolder; width: auto; min-width: 4rem; text-align: center;' data-brackets",
                        pad_e = "></span>)</span>",
                        pad = "",
                        date = new Date(),
                        test_time = Date.now(),
                        cur_time = this.cur_time,
                        checkpoint = this.checkpoint,
                        es_time = 0;
                    cur_time.flag = setInterval(function () {
                        es_time = Date.now() - test_time;
                        cur_time.es_time = es_time;
                        date.setTime(es_time)
                        cur_time.time = pad_zero(date.getHours() - 8) + ":" + pad_zero(date.getMinutes()) + ":" + pad_zero(date.getSeconds());

                        /*限时赛特殊处理*/
                        if (checkpoint.time_limit) {
                            if (checkpoint.time_limit <= es_time) {//测试结束
                                this.finish();
                                clearInterval(cur_time.flag);
                            }
                        }

                    }, 500)

                    this.questions.map(el => {
                        el.val = el.answer = "";

                        /*初始化题型*/
                        switch (el.type) {
                            case "cho":
                                el.type_text = "单选题";
                                pad = pad_s + pad_e;
                                break;
                            case "com":
                                el.type_text = "填空题";
                                pad = pad_s + " contenteditable='true' " + pad_e;
                                break;
                            case "jud":
                                el.type_text = "判断题";
                                el.item = ["正确", "错误"];
                                pad = pad_s + pad_e;
                        }

                        /*初始化填空*/
                        if (el.text.search("(\\?)") > 0) {//替换( )
                            el.text = el.text.replace(/\(\?\)/g, pad);
                        } else {//末尾追加( )
                            el.text += pad;
                        }

                    })
                } else {//检查试卷
                    if (this.test_btn.text == "继续做题") {//继续做题
                        this.test_btn.text = "检查试卷";
                        this.test_btn.test_state = "do_test";
                    } else {//检查试卷
                        this.test_btn.text = "继续做题";
                        this.test_btn.test_state = "check_test";
                        this.write_answer();
                    }
                }
            },

            /*提交试卷*/
            send_test() {
                clearInterval(this.cur_time.flag);
                this.finish();
            },

            /*切换试题*/
            toggle_question(dir) {
                this.write_answer();
                if (dir == "-1" && this.cur_question_number == 1) return;
                if (dir == "1" && this.cur_question_number == this.questions.length) return;
                this.cur_question_number += dir * 1;
                setTimeout(() => {//填写答案
                    document.querySelector(".question-widget [data-brackets]").innerHTML = this.cur_question.answer;
                })
            },

            /*选择答题*/
            cho_answer(e) {
                if (e.target.nodeName.toLowerCase() === "input") {
                    setTimeout(() => {//设置答题
                        document.querySelector(".question-widget [data-brackets]").innerHTML = this.set_answer(this.cur_question.val);
                    })
                }
            },

            /*填空答题*/
            typin_answer(e){
                this.pre_questions[this.cur_question_number - 1].answer = e.target.innerHTML;//设置数据
            },

            /*格式化答案*/
            set_answer(val) {
                let question = this.cur_question;
                question.answer = this.answers[question.type][val - 1];//单选、判断
                return question.answer;
            },

            /*结束答题*/
            finish(){
                this.working = false;
                this.test_btn.test_state = "check_test";
                this.write_answer();

                /*验证试卷*/
                this.questions.forEach( que => {
                    if(que.answer === "√"){
                        que.answer = "正确";
                    }else if(que.answer === "×"){
                        que.answer = "错误";
                    }
                    let standard_answer = this.standard_answers[que.question_id],
                        type;
                    if(standard_answer.answer !== que.answer){//错误答案
                        if(que.type === "cho"){//选择题
                            type = 1;
                        }else if(que.type === "jud"){//判断题
                            type = 2;
                        }else if(que.type === "com"){//填空题
                            type = 3;
                        }
                        que.standard_answer = "正确答案: “" + standard_answer.answer + "”。&nbsp;&nbsp;&nbsp;&nbsp;" + standard_answer.explain;//格式化结果

                        /*记录错误结果数据*/
                        this.$axios.post("user_practice_create/", {
                            chapter: this.$route.query.cid,
                            practice: que.question_id,
                            types: type,
                            end_time: this.$utils.gen_date_time(),
                            practice_info: que.answer
                        })

                    }
                })

            },

            /*填写试卷*/
            write_answer(){
                let answer = document.querySelector(".question-widget [data-brackets]").innerHTML,
                    id = document.querySelector(".question-widget").id;
                document.querySelector("[data-id='" + id + "'] [data-brackets]").innerHTML = answer;//填写试卷答案
            },

            /*返回*/
            back(){
                this.$router.go(-1);
            }

        },
        computed: {

            /*当前试题*/
            cur_question() {
                let question = this.questions[this.cur_question_number - 1];
                question.number = this.cur_question_number;//序号
                return question;
            },

        },
        mounted() {
            this.$axios.get("practice/?chapter=" + this.$route.query.cid)
                .then(p_res => {
                    p_res.data.forEach( res => {
                        let type,
                            text = res.data,
                            item,
                            data;
                        switch(res._type*1){//初始化题型
                            case 1: type = "cho";
                                let text_arr = res.data.split("：");
                                text = text_arr[0];
                                item = text_arr[1].split(/[A-G] /);
                                item.shift();
                                break;
                            case 2: type = "jud";
                                break;
                            case 3: type = "com";
                                break;
                        }
                        data = {
                            question_id: res.id,
                            type,
                            text,
                        };
                        if(item) data.item = item;//选择题选项
                        this.pre_questions.push(data);
                        this.standard_answers[res.id] = {//答案集合
                            answer: res.standard_answer,
                            explain: res.standard_explain
                        }
                    })
                })
        }
    }
</script>

<style scoped>
    @import "../assets/css/pannel-widget.css";
    @import "../assets/css/base-title-widget.css";
    @import "../assets/css/seg-widget.css";
    @import "../assets/css/basic-article-widget.css";
    @import "../assets/css/list-text-widget.css";
    @import "../assets/css/base-btn-widget.css";
    @import "../assets/css/no-result-widget.css";
    @import "../assets/css/question-widget.css";
    @import "../assets/css/test-paper-widget.css";
    @import "../assets/css/main-title-widget.css";
</style>


