class ClassManager{
    constructor(user_learn_progress, class_info_data){
        this.user_learn_progress = user_learn_progress;
        this.class_info = class_info_data;
        this._gen_class_outline();
    }

    /*获取课程大纲*/
    get_class_outline(){
        return this.class_info;
    }

    /*获取当前课程信息*/
    get_cur_class(){
        return this.cur_class;
    }

    /*生成课程大纲*/
    _gen_class_outline(){
        var _this = this,
            user_learn_progress = this.user_learn_progress,
            lasted = user_learn_progress.lasted;

        this.class_info.list.forEach(function(item){
            item.datas.forEach(function(data){
                data.rank = user_learn_progress[data.id] || "";
                if(data.id === lasted){
                    _this.cur_class = data;//当前课程
                    data.select = "select";
                }
            })
        })
        if(!this.cur_class){
            this.class_info.list[0].select = "select";
            this.cur_class = this.class_info.list[0].datas[0];//默认显示首个课程
        }
    }
}

export default ClassManager;



