<template>
    <div class="container">
        <h2 class="main-title-widget">
            <router-link to="/arena_battle_mode" class="mt-w-link">返回对战</router-link>
            <span class="mt-w-text">{{checkpoint.name}}</span>
        </h2>
        <div class="grid">
            <div class="col-8">
                <div class="pannel-widget">
                    <div class="typing-widget">
                        <div class="t-w-words" ref="t_w_words">
                            <p class="t-w-p">
                                <span v-for="word of checkpoint.text">{{word}}</span></p>
                        </div>
                        <div class="t-w-notic">打字输入区</div>
                        <textarea class="t-w-textarea" ref="t_w_textarea" spellcheck="false" @input="start_test"></textarea>
                    </div>
                </div>
            </div>
            <div class="col-4 pad-left-2">
                <div v-if="checkpoint.name">
                    <div class="pannel-widget">
                        <h3 class="base-title-widget bt-w-blue">{{checkpoint.name}}</h3>
                        <hr class="seg-widget"/>
                        <div class="basic-article-widget pad-w-2">
                            <p class="ba-w-p">{{checkpoint.desc}}</p>
                        </div>
                    </div>
                    <div class="pad-h-1"></div>
                </div>

                <div class="pannel-widget">
                    <h3 class="base-title-widget bt-w-blue">测试信息</h3>
                    <div class="list-text-widget grid pad-w-2">
                        <div class="col-12">
                            <div class="lt-w-item">
                                <span>{{cur_time.time}}</span>
                                <span class="lt-w-item-after" v-if="checkpoint.time_limit">{{checkpoint.time_limit_format}} 分钟</span>
                            </div>
                        </div>
                    </div>
                    <div class="pcenter">
                        <div class="pad-h-2"></div>
                        <span class="base-btn-widget" :state="submit_btn_state" @click="send_test" ref="submit_btn">提交代码</span>
                        <div class="pad-h-1"></div>
                    </div>
                </div>
            </div>
        </div>

        <div class="pad-h-4"></div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import { codemirror } from 'vue-codemirror-lite'
    require('codemirror/mode/javascript/javascript')
    require('codemirror/theme/darcula.css')

    var pad_zero = val => {
        return ("" + val).length == 1 ? "0" + val : val;
    };

    export default {
        name: "coding",
        data() {
            return {

                /*是否开始编程*/
                if_start: false,

                /*编程试题*/
                checkpoint: {},

                /*起始时间*/
                cur_time: {
                    flag: null,
                    time: "00:00:00",
                    es_time: 0,
                },

                /*提交按钮状态*/
                submit_btn_state: "normal",

                /*错字数*/
                result: {
                    type_count: 0,
                    error_count: 0,
                }

            }
        },
        methods: {

            /*开始测试*/
            start_test(e) {
                if(this.if_start === false){
                    this.if_start = true;

                    /*实时显示时间*/
                    let date = new Date(),
                        test_time = Date.now(),
                        cur_time = this.cur_time,
                        checkpoint = this.checkpoint,
                        es_time = 0;
                    cur_time.flag = setInterval(() => {
                        es_time = Date.now() - test_time;
                        cur_time.es_time = es_time;
                        date.setTime(es_time)
                        cur_time.time = pad_zero(date.getHours() - 8) + ":" + pad_zero(date.getMinutes()) + ":" + pad_zero(date.getSeconds());

                        /*限时赛特殊处理*/
                        if (checkpoint.time_limit) {
                            if (checkpoint.time_limit <= es_time) {//测试结束
                                clearInterval(cur_time.flag);
                                this.send_test();
                            }
                        }

                    }, 500)

                }

                /*输入（打字）逻辑*/
                let cur_point = this.result.type_count = this.$refs.t_w_textarea.value.length,
                    max_len = this.$refs.t_w_words.querySelectorAll("span").length,
                    word = "";
                this.result.error_count = 0;
                for(let i = 0; i < cur_point; i++){
                    word = this.$refs.t_w_words.querySelectorAll("span")[i]
                    if(this.$refs.t_w_textarea.value[i] === word.innerHTML){//正确
                        word.setAttribute("state", "right");
                    }else{//错误
                        this.result.error_count++;
                        word.setAttribute("state", "error");
                    }
                }
                for(let k = cur_point; k < max_len; k++){//清除状态
                    word = this.$refs.t_w_words.querySelectorAll("span")[k];
                    if(word){
                        word.removeAttribute("state");
                    }else{
                        break;
                    }
                }

            },

            /*提交试卷*/
            send_test() {
                if(this.submit_btn_state === "normal"){
                    clearInterval(this.cur_time.flag);
                    this.submit_btn_state = "visied";
                    this.$refs.submit_btn.innerHTML = "已提交";
                    console.log({
                        type_count: this.result.type_count,
                        error_count: this.result.error_count,
                        used_time: this.cur_time.es_time
                    });
                }
            }

        },
        beforeMount(){
            /*this.$axios.get("pass/" + this.$route.query.id)
                .then(res => {
                    let pass = res.data,
                        checkpoint = {
                            checkpoint_id: pass.id,
                            name: pass.name,
                            desc: pass.pass_intr,
                        };
                    this.checkpoint = checkpoint;

                    /!*关卡介绍加工*!/
                    if (pass && pass.pass_limit_time > 0) {
                        checkpoint.time_limit_format = pass.pass_limit_time;
                        checkpoint.time_limit = pass.pass_limit_time * 60000;
                    }

                })*/

            this.checkpoint = {//假数据
                checkpoint_id: 1,
                name: "快速打字",
                desc: "打字速度赛，看谁打的快！",
                text: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean euismod bibendum laoreet. Proin gravida dolor sit amet lacus accumsan et viverra justo commodo. Proin sodales pulvinar.tempor. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nam fermentum, nulla luctus pharetra vulputate."
            };

        },
        mounted() {
            this.$refs.t_w_textarea.style.height = this.$refs.t_w_words.clientHeight + "px";
        }
    }
</script>

<style scoped>
    @import "../assets/css/pannel-widget.css";
    @import "../assets/css/base-title-widget.css";
    @import "../assets/css/seg-widget.css";
    @import "../assets/css/basic-article-widget.css";
    @import "../assets/css/list-text-widget.css";
    @import "../assets/css/base-btn-widget.css";
    @import "../assets/css/main-title-widget.css";
    @import "../assets/css/typing-widget.css";
</style>


